import React, { useEffect } from 'react'
import "../Styling/HomePage.css"
import pic from "../Icons/181632.jpg"
import pic1 from "../Icons/index.jpg"
import pic2 from "../Icons/pic2.jpg"
import pic3 from "../Icons/pic3.jpg"
import left from "../Icons/left-chev.png"
import right from "../Icons/right-chev.png"
function DefaultHome() {
    let count=0;
    function swapImages(event)
    {
        if(document.getElementById("Button1")==null)
        {
            console.log(count)
            clearInterval(myInterval)
            count=0
            return;
        }
        if(event=="But1")
        {
            document.getElementById("imgdisplay").src=pic
            document.getElementById("Button1").style.backgroundColor="grey"
            document.getElementById("Button2").style.backgroundColor="silver"
            document.getElementById("Button3").style.backgroundColor="silver"
            document.getElementById("Button4").style.backgroundColor="silver"

        }
        else if(event=="But2")
        {
            document.getElementById("imgdisplay").src=pic1
            document.getElementById("Button1").style.backgroundColor="silver"
            document.getElementById("Button2").style.backgroundColor="grey"
            document.getElementById("Button3").style.backgroundColor="silver"
            document.getElementById("Button4").style.backgroundColor="silver"
        }
        else if(event=="But3")
        {
            document.getElementById("imgdisplay").src=pic2
            document.getElementById("Button1").style.backgroundColor="silver"
            document.getElementById("Button2").style.backgroundColor="silver"
            document.getElementById("Button3").style.backgroundColor="grey"
            document.getElementById("Button4").style.backgroundColor="silver"
        }
        else
        {
            document.getElementById("imgdisplay").src=pic3
            document.getElementById("Button1").style.backgroundColor="silver"
            document.getElementById("Button2").style.backgroundColor="silver"
            document.getElementById("Button3").style.backgroundColor="silver"
            document.getElementById("Button4").style.backgroundColor="grey"
        }
        if(count==4)
        {
            count=0
        }
    }
    let myInterval=setInterval(()=>swapImages("But"+(count++)),4000)

    return (
        <div>
            <div className="HomePage">
            <img id="imgdisplay" src={pic} style={{height:"70vh",width:"95vw",overflow:"hidden",borderRadius:"20px"}}></img>
            </div>
            <div className="MovingButtons">
                <button className="Button" id="Button1" onClick={()=>swapImages("But1")}></button>
                <button className="Button" id="Button2" onClick={()=>swapImages("But2")}></button>
                <button className="Button" id="Button3" onClick={()=>swapImages("But3")}></button>
                <button className="Button" id="Button4" onClick={()=>swapImages("But4")}></button>
            </div>
            <h2 className="Trending">
                Trending Paintings
            </h2>
            <div className="Products">
            <div className="Pointers">
                <img className="icons" src={left}></img>
            </div>
                <div className="Product">
                    <img className="Pic" src={pic1}></img>
                   
                    <div className="Desc">Description</div>
                </div>
                <div className="Product">
                <img className="Pic" src={pic2}></img>
                   
                   <div className="Desc">Description</div>
                </div>
                <div className="Product">
                <img className="Pic" src={pic3}></img>
                   
                   <div className="Desc">Description</div>
                </div>
                <div className="Pointers">
                <img  className="icons" src={right}></img>
                </div>
            </div>
            <h2 className="Trending">
                Trending Books
            </h2>
            <div className="Products">
                <div className="Pointers">
                <img className="icons" src={left}></img>
                </div>
                <div className="Product">
                    <img className="Pic" src={pic1}></img>
                   
                    <div className="Desc">Oil Painting<br></br></div>
                </div>
                <div className="Product">
                    <img className="Pic" src={pic1}></img>
                   
                    <div className="Desc">Description</div>
                </div>
                <div className="Product">
                    <img className="Pic" src={pic1}></img>
                   
                    <div className="Desc">Description</div>
                </div>
                <div className="Pointers">
                <img  className="icons" src={right}></img>
                </div>
            </div>
        </div>
    )
}

export default DefaultHome
