import React from 'react'
import  '../Styling/Navbar.css'
import wishicon from '../Icons/heart.png'
import carticon from '../Icons/add-to-basket.png'
import menu from '../Icons/menu.png'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import HomePage from './HomePage';
function NavBar() {
    function myFunction()
    {
        document.getElementById("myDropdown").classList.toggle("show");
    }
    
    window.onclick = function(event) {
        console.log("click!!")
        console.log(event.target.className)
        if (event.target.className!="dropbtn") {
          var dropdowns = document.getElementsByClassName("dropdown-content");
          var i;
          for (i = 0; i < dropdowns.length; i++) {
            var openDropdown = dropdowns[i];
            if (openDropdown.classList.contains('show')) {
              openDropdown.classList.remove('show');
            }
          }
        }
      } 
      
    return (
<div>
    <div className="Navbar">
        <div className="LogoNav">
       <div className="NavbarTabs" style={{width:"130px","display":"flex","padding-left":"0px"}}id="Logo">
             <label>A r t i f y.</label>
       </div>
       </div>
       <div className="LeftNav">
       <div className="NavbarTabs">
           <Link to='/' className="Links">
               <label>Home</label>
            </Link>
        </div>
        <div className="NavbarTabs">
           |
        </div>
       <div className="NavbarTabs">
           <Link to='/Books' className="Links">
               <label>Books</label>
            </Link>
        </div>
        <div className="NavbarTabs">
           |
        </div>
        <div className="NavbarTabs">
            <Link to='/Paintings' className="Links">
                 <label>Paintings</label>
            </Link>
       </div>
       </div>
       <div className="MidNav">
       <div className="NavbarTabs">
           <input id="Searbar" type="search" placeholder="Search..." style={{width:'100px',height:'30px',display:'flex'}}></input>
       </div>
       </div>
       <div className="RightNav">
       <div className="NavbarTabs">
       <Link to='/Signup' className="Links">
                 <label>SignUp</label>
            </Link>
       </div>
       <div className="NavbarTabs">
       |
       </div>
       <div className="NavbarTabs">
       <Link to='/Login' className="Links">
                 <label>Login</label>
            </Link>
       </div>
       <div className="NavbarTabs">
       <img classname="icon" src={wishicon} style={{height:"20px"}}></img>
       </div>
       <div className="NavbarTabs">
       <img classname="icon" src={carticon} style={{height:"20px"}}></img>
       </div>
       </div>
       <div className="ResponsiveBar">
           <button onClick={()=>myFunction()} className="dropbtn" style={{cursor:"pointer"}}>
           <div className="Hamburger"></div>
           <div className="Hamburger"></div>
           <div className="Hamburger"></div> 
           </button>
           <div id="myDropdown" class="dropdown-content">
               <div className="RespOpts">
               <div>
                   Welcome Guest!!
               </div>
               <div>
                   Login/SignUp
               </div>
               </div>
               <div className="RespOpts">
                   <div>
                   <Link to='/' className="Links">
               <label>Home</label>
            </Link>
                   </div>
                   <div>
                   <Link to='/Books' className="Links">
               <label>Books</label>
            </Link>
                   </div>
                   <div>
                   <Link to='/Painintgs' className="Links">
               <label>Painting</label>
            </Link>
                   </div>
               </div>
           </div>
       </div>
    </div>
       
       <Route exact path='/Books'>
           <HomePage selected="Books"/>
       </Route>
       <Route exact path='/'>
           <HomePage selected="Default"/>
       </Route>
       <Route exact path='/Paintings'>
           <HomePage selected="Paintings"/>
       </Route>
       <Route exact path='/Login'>
           <HomePage selected="Login"/>
       </Route>
       <Route exact path="/Team">
           <HomePage selected="Team"/>
       </Route>
</div>
        )
}

export default NavBar

